import * as types from './types'
import { SIGNOUT_SUCCESS } from '../users/types'

const initialState = {
  zoomAccessToken: '',
  zoomRefreshToken: '',
  zoomUser: null,
  zoomMeetings: null
}

const zoomData = (state = initialState, action) => {
  switch (action.type) {
    //REGISTER
    case types.GET_ACCESS_TOKEN_SUCCESS:
      return { ...state, zoomAccessToken: action.zoomData.zoomAccessToken, zoomRefreshToken: action.zoomData.zoomRefreshToken, zoomUser: action.zoomData.zoomUser, zoomMeetings: action.zoomData.zoomMeetings }

    case types.GET_ZOOM_USERINFO_SUCCESS:
      console.log("zoomreducer", action)
      return { ...state, zoomUser: action.zoomUser }

    case types.GET_ZOOM_USER_MEETINGS_SUCCESS:
      console.log('zoom reducer', action)
      return { ...state, zoomMeetings: action.zoomMeetings }

    case SIGNOUT_SUCCESS:
      return initialState

    default:
      return state
  }
}

export default zoomData
