import * as types from './types'
import { apiUrl } from '../../config'

export const getAccessToken = (token, code) => {
  return (dispatch) => {
    fetch(apiUrl.GET_ACCESS_TOKEN, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': "Bearer " + token,
      },
      body: JSON.stringify({
        code: code
      })
    })
      .then(response => response.json())
      .then(data => {
        if (!data.error) {
          dispatch(getAccessTokenSuccess(data.zoomData));
        } else {
          dispatch(getAccessTokenError(data.errorObj))
        }
      })
      .catch(error => {
        // dispatch(getAccessTokenError(error))
        console.log(error)
      })
  }
}

export const getZoomUserInfo = (token, zoomAccessToken) => {
  return (dispatch) => {
    fetch(apiUrl.GET_ZOOM_USERINFO, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': "Bearer " + token,
      },
      body: JSON.stringify({
        zoomAccessToken
      })
    })
      .then(response => response.json())
      .then(data => {
        console.log(data)
        if (!data.error) {
          dispatch(getZoomUserInfoSuccess(data.zoomUser));
        } else {
          dispatch(getZoomUserInfoError(data.errorObj))
        }
      })
      .catch(error => {
        // dispatch(getAccessTokenError(error))
        console.log(error)
      })
  }
}

export const getZoomUserMeetings = (token, zoomAccessToken, userId) => {
  return (dispatch) => {
    fetch(apiUrl.GET_ZOOM_USER_MEETINGS, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
        'Authorization': "Bearer " + token,
      },
      body: JSON.stringify({
        zoomAccessToken,
        userId
      })
    })
      .then(response => response.json())
      .then(data => {
        if (!data.error) {
          console.log(data)
          dispatch(getZoomUserInfoSuccess(data.zoomUser));
        } else {
          dispatch(getZoomUserInfoError(data.errorObj))
        }
      })
      .catch(error => {
        // dispatch(getAccessTokenError(error))
        console.log(error)
      })
  }
}

export const getAccessTokenSuccess = (zoomData) => ({
  type: types.GET_ACCESS_TOKEN_SUCCESS,
  zoomData
})

export const getAccessTokenError = error => ({
  type: types.GET_ACCESS_TOKEN_ERROR,
  error
})

export const getZoomUserInfoSuccess = (zoomUser) => ({
  type: types.GET_ZOOM_USERINFO_SUCCESS,
  zoomUser
})

export const getZoomUserInfoError = error => ({
  type: types.GET_ZOOM_USERINFO_ERROR,
  error
})

export const getZoomUserMeetingSuccess = (zoomMeetings) => ({
  type: types.GET_ZOOM_USER_MEETINGS_SUCCESS,
  zoomMeetings
})

export const getZoomUserMeetingsError = error => ({
  type: types.GET_ZOOM_USER_MEETINGS_ERROR,
  error
})


